.image-used-icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.create-an-account {
  margin: 0;
  position: relative;
  font-size: inherit;
  text-transform: capitalize;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  width: 33.31rem;
  height: 4.38rem;
  flex-shrink: 0;
}
.flat-color-iconsgoogle {
  position: relative;
  width: 3rem;
  height: 3rem;
  overflow: hidden;
  flex-shrink: 0;
}
.sign-up-with {
  margin: 0;
  flex: 1;
  position: relative;
  letter-spacing: 0.5px;
  line-height: 2.25rem;
  display: flex;
  align-items: center;
  height: 2.25rem;
}
.sign-up-with-google1 {
  align-self: stretch;
  border-radius: var(--br-mini);
  border: 1px solid var(--color-gray-200);
  box-sizing: border-box;
  height: 5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-5xl);
  gap: var(--gap-29xl);
}
.or {
  margin: 0;
  align-self: stretch;
  position: relative;
  letter-spacing: 0.5px;
  line-height: 2.25rem;
}
.option,
.sign-up-with-google {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.option {
  justify-content: center;
  text-align: center;
  font-size: 1.5rem;
}
.sign-up-with-google {
  justify-content: flex-start;
  gap: var(--gap-29xl);
}
.password,
.username {
  font-family: var(--font-inter);
  font-size: var(--font-size-9xl);
  background-color: transparent;
  align-self: stretch;
}
.username {
  border: 1px solid var(--color-gray-200);
  border-radius: var(--br-mini);
  box-sizing: border-box;
  height: 5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-5xl);
}
.password {
  border: 0;
}
.input-field,
.password,
.sign-up-input {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.input-field {
  align-self: stretch;
  gap: 1.5rem;
}
.sign-up-input {
  width: 33rem;
  gap: 3.75rem;
  text-align: left;
  font-size: var(--font-size-9xl);
}
.create-account1 {
  text-decoration: none;
  flex: 1;
  position: relative;
  font-size: var(--font-size-9xl);
  letter-spacing: 0.5px;
  line-height: 2.25rem;
  font-family: var(--font-inter);
  color: #fff;
  text-align: center;
}
.create-account-button {
  cursor: pointer;
  border: 0;
  padding: 1rem 1.25rem;
  background-color: #4c14e2;
  border-radius: var(--br-mini);
  width: 33.31rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.create-account {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 3.5rem;
  text-align: center;
  font-size: 3.5rem;
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
.create-an-account-page {
  position: relative;
  background: linear-gradient(
      173.27deg,
      rgba(84, 35, 215, 0.24),
      rgba(31, 60, 167, 0) 99.79%
    ),
    #fff;
  width: 100%;
  height: 76.13rem;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 1.63rem;
  cursor: pointer;
}
